.hearings-status {
    display: flex;
    align-items: center;
    padding: 8px 23px;
    padding-right: 11px;
    border: 1px solid var(--color-stroke, $light-grey);
    border-radius: $border-radius;

    @include mobile {
        flex-direction: column;
        padding: 0;
    }

    p {
        margin: 0;
        margin-right: auto;
    
        font-weight: 500;
        font-size: rem(15px);
        line-height: 135%;

        @include mobile {
            width: 100%;
            padding: 16px;
            border-bottom: 1px solid #E3E8EE;
            border-radius: 5px;
        }
    
        span {
            margin-left: 5px;
            font-weight: 400;
            color: #2C8ECC;

            @include mobile {
                display: block;
                margin-left: 0;
                margin-top: 10px;
            }
        }
    }
    
    .button-star {
        @include mobile {
            display: flex;
            align-items: center;
            padding: 15px 5px;
        }

        svg {
            width: 24px;
            height: 24px;

            @include mobile {
                margin-right: 12px;
            }
        }
    }
}
