.commentary-add {
    margin-bottom: 50px;
    padding: 40px 32px 32px 32px;
    background-color: #F9F9FA;
    border-radius: $border-radius;

    @include tablet {
        margin-bottom: 40px;
    }

    @include mobile {
        padding: 16px;
    }

    h2 {
        margin-top: 0;
        margin-bottom: 16px;
        font-size: rem(30px);
        line-height: rem(35.55px);
        font-weight: 400;

        @include mobile {
            margin-bottom: 0px;
            font-size: rem(24px);
            line-height: rem(28.44px);
        }
    }
}

.commentary-add__description {
    margin-bottom: 40px;

    @include mobile {
        margin-bottom: 23px;
        font-size: rem(13px);
        line-height: rem(17px);
    }

    p:first-child {
        @include mobile {
            margin-top: 8px;
        }
    }
}

.commentary-add__textarea {
    position: relative;
    margin-bottom: 25px;

    @include mobile {
        margin-bottom: 5px;
    }

    & textarea {
        font-family: inherit;
        font-size: rem(15px);
        line-height: rem(27px);
        color: inherit;
        width: 100%;
        padding: 12px 16px;
        -webkit-appearance: none;
        border: 1px solid var(--color-stroke, $light-grey);
        border-radius: 5px;
        resize: none;
    
        &::placeholder {
            font-size: rem(15px);
            line-height: rem(28px);
            color: var(--color-text, $grey-default);
        }

        @include mobile {
            height: 160px;
        }
    }

    p {
        display: none;
        position: absolute;
        right: 1px;
        bottom: -27px;
        font-size: rem(13px);
        line-height: rem(16.9px);
        color: #A6ABB2;

        @include mobile {
            bottom: -22px;
            font-size: rem(11px);
            line-height: rem(16.9px);
        }
    }

    &--warning {
        p {
            display: block;
        }
    }

    &--error {
        & textarea {
            border: 1px solid #F3A8A8;
        }

        p {
            color: #E55050;
        }
    }
}

.commentary-add__bottom-wrapper {
    display: flex;

    @include mobile {
        flex-direction: column;
    }

    p {
        padding-top: 7px;
        max-width: 460px;
        font-size: rem(13px);
        line-height: rem(16.9px);
        color: #A6ABB2;
        
        @include mobile {
            line-height: rem(17px);
        }

        &:first-child {
            margin: 0;
        }
    }
}

.commentary-add__button {
    display: block;
    width: 175px;
    padding: 17px 26px;
    margin-left: auto;

    @include mobile {
        margin-top: 12px;
        margin-left: 0;
        padding: 16px 26px;
        width: 100%;
    }
}